import { useSelector, useDispatch } from "react-redux";
import { addToCart, updateQuantity, removeFromCart } from "../store/cartSlice";

export default function ProductCard({ product }) {
  const cart = useSelector((state) => state.cart);
  const dispatch = useDispatch();

  const handleQuantityChange = (e) => {
    const value = parseInt(e.target.value) || 0;
    dispatch(updateQuantity({ id: product.id, quantity: value }));
  };

  const handleRemove = () => {
    dispatch(removeFromCart(product.id));
  };

  return (
    <div className="bg-white p-6 rounded-xl shadow-md hover:shadow-lg transition duration-300">
      <h2 className="text-xl font-semibold text-gray-800">{product.name}</h2>
      <p className="text-gray-600 mb-4">${product.price}</p>

      {!cart[product.id] ? (
        <button
          className="w-full bg-indigo-600 text-white py-2 px-4 rounded hover:bg-indigo-700 transition"
          onClick={() => dispatch(addToCart(product.id))}
        >
          Add to Cart
        </button>
      ) : (
        <div className="mt-4 space-y-2">
          <div className="flex items-center gap-2">
            <label className="text-sm text-gray-600">Quantity:</label>
            <input
              type="number"
              value={cart[product.id]}
              onChange={handleQuantityChange}
              className="w-16 border border-gray-300 rounded px-2 py-1 text-black focus:ring-2 focus:ring-indigo-500 outline-none"
            />
          </div>
          <button
            className="w-full bg-red-500 text-white py-2 px-4 rounded hover:bg-red-600 transition"
            onClick={handleRemove}
          >
            Remove from Cart
          </button>
        </div>
      )}
    </div>
  );
}
